.benefit-block {
  background-color: #222224;
  padding-top: 48px;
  padding-bottom: 16px;
  @include md {
    padding-top: 48px;
    padding-bottom: 16px; }
  @include lg {
    padding: 103px 0; }

  &__items {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    @include md {
      flex-direction: column; }

    @include lg {
      flex-direction: row; } }




  .benefit__item {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-bottom: 48px;
    @include md {
      max-width: 260px;
      margin-bottom: 48px; }
    @include lg {
      max-width: 100%;
      margin-bottom: 0; }
    &-img {
      max-width: 100%;
      text-align: center;
      margin-bottom: 28px; }
    &-title {
      font-weight: 400;
      font-size: 20px;
      line-height: 24px;
      color: #FBFBFB;
      text-align: center;
      margin-bottom: 16px; }
    &-subtitle {
      font-weight: 300;
      font-size: 14px;
      line-height: 17px;
      text-align: center;
      color: #FBFBFB; } } }








