.items-block-catalog {
  margin-top: 24px;
  @include lg {
    margin-top: 50px; }

  &__main-title {
    text-align: center;
    h2 {
      font-weight: 400;
      font-size: 30px;
      line-height: 36px;
      color: #222222; }
    & span {
      font-weight: 400;
      font-size: 14px;
      line-height: 17px;
      color: #9F9F9F; } }


  .items-block__cards {
    margin: 48px 0;
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    grid-template-rows: 1fr 1fr;
    gap: 1rem;
    @include md {
      grid-template-columns: repeat(2, 360px);
      gap: 1rem; }
    @include lg {
      grid-template-columns: repeat(3, 360px);
      gap: 2rem; }

    .item-card {
      display: flex;
      flex-direction: column;
      max-width: 100%;
      @include md {
        max-width: 360px; }
      @include lg {
        max-width: 360px; }
      &:hover {
        .img-block:after {
          position: absolute;
          content: '';
          top: 0;
          bottom: 0;
          left: 0;
          right: 0;
          background-color: rgba(58, 56, 56, 0.86); }
        .img-block__cart {
          display: flex; } } }

    .item-card__body {
      background: #F8F8F8;
      padding: 24px 28px 20px 16px; }

    .img-block {
      position: relative; }
    .img-block img {
      object-fit: cover;
      width: 100%; }

    .img-block__cart {
      position: absolute;
      z-index: 1;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      justify-content: center;
      align-items: center;
      display: none;
      &-title {
        cursor: pointer;
        position: relative;
        text-decoration: none;
        color: white;
        border: 1px solid white;
        line-height: 44px;
        font-weight: 400;
        font-size: 14px;
        padding-left: 50px;
        padding-right: 1rem;
        &:before {
          position: absolute;
          content: '';
          top: 50%;
          left: 13px;
          transform: translate(0, -50%);
          width: 26px;
          height: 24px;
          background-image: url('../img/item-card/cart.svg'); } } }



    .card__title {
      font-weight: 400;
      font-size: 13px;
      line-height: 16px;
      color: #000000;
      margin-bottom: 12px; }

    .card__subtitle {
      font-weight: 300;
      font-size: 14px;
      line-height: 17px;
      color: #5D5D5D;
      margin-bottom: 18px; }

    .card__price {
      font-weight: 400;
      font-size: 16px;
      line-height: 19px;
      color: #F16D7F; } }


  .btn-all-product {
    display: inline-block;
    padding: 0 39px;
    line-height: 48px;
    border: 1px solid #FF6A6A;
    font-weight: 400;
    font-size: 16px;
    color: #F26376;
    text-decoration: none;
    text-align: center;
    &:hover {
      background: #F16D7F;
      border: 1px solid #FF6A6A;
      color: #fff; } }


  &__btn {
    display: flex;
    justify-content: center;
    margin-bottom: 100px; } }


.items-block__filter {
  display: flex;
  justify-content: flex-end;
  position: relative;
  margin-bottom: 40px;
  @include md {
    margin-bottom: 40px;
    justify-content: flex-end; }
  @include lg {
    margin-bottom: 64px;
    justify-content: center; }

  &-sort {
    display: flex;
    align-items: center;
    position: absolute;
    left: 0;
    font-weight: 600;
    font-size: 14px;
    line-height: 17px;
    color: #000000;
    top: 50%;
    transform: translateY(-50%);
    & .filter-text {
        display: none; }
    @include md {
      .filter-text {
        display: block; } }
    & .sort-btn {
      margin-left: 0;
      & img {
        width: 37px;
        @include md {
          width: 15px; } }
      @include md {
        margin-left: 10px; } } }

  &-wrap {
    display: flex;
    &-title {
      display: flex;
      align-items: center;
      font-weight: 400;
      font-size: 14px;
      line-height: 17px;
      color: #6F6E6E;
      margin-left: 28px;
      & a {
        display: flex;
        justify-content: center;
        margin-left: 10px; } } } }

.paginate-block__btn {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 63px;
  @include lg {
    margin-bottom: 96px; }

  .paginate-block__wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #EBEBEB;
    border-radius: 5px;
    & a {
      margin: 0 10px;
      padding-top: 12px;
      padding-bottom: 14px;
      text-decoration: none; }

    & a:first-child {
      font-weight: 300;
      font-size: 16px;
      line-height: 19px;
      color: #EF5B70;
      margin-left: 1rem; }
    & a:not(first-child) {
      font-weight: 300;
      font-size: 16px;
      line-height: 19px;
      color: #C4C4C4; }
    & a:nth-last-child(2) {
      margin-left: 0; }
    & a:nth-last-child(3) {
      margin-right: 0;
      margin-left: 0; }
    & a:nth-last-child(4) {
      margin-right: 0; }
    & a:last-child {
      margin-right: 1rem; } } }
