.new-arrival {
  background-color: #F8F3F4;
  padding: 24px 0 46px;
  @include md {
    padding: 60px 0; }
  @include lg {
    padding: 65px 0; }



  .block-arrival {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
    @include md {
      flex-direction: row; }
    @include lg;

    &__title {
      font-weight: 400;
      font-size: 24px;
      line-height: 29px;
      color: #F16D7F;
      margin-bottom: 32px;
      @include md {
        margin-bottom: 0; } }

    &__link {
      font-weight: 300;
      font-size: 14px;
      line-height: 17px;
      color: #636363;
      text-decoration: none;
      margin: 0 3px; }

    &__link.active {
      font-weight: 700;
      font-size: 14px;
      line-height: 17px;
      color: #F16D7F; } }





  .bread-crumb {
    display: flex; } }
