.product-slider {
  .container-slider {
    position: relative;
    height: 624px;
    overflow: hidden;
    @include md;
    @include lg {
      height: 777px; }
    @media(min-width: 1546px) {
      height: auto;
      width: 100%; }
    img {
      object-fit: cover;
      object-position: center center;
      height: 100%;
      @media(min-width: 1546px) {
        height: auto;
        width: 100%; } }

    &__arrow-left {
      width: 47px;
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      cursor: pointer; }

    &__arrow-right {
      width: 47px;
      position: absolute;
      top: 50%;
      right: 0;
      transform: translateY(-50%);
      cursor: pointer; } } }
