.footer-block {
  background-image: linear-gradient(rgba(255, 255, 255, 0.6), rgba(255, 255, 255, 0.6)), url('../img/bg-footer.jpg');
  background-position: 28% center;
  background-size: auto 100%;
  background-repeat: no-repeat;

  height: 550px;
  @include md {
    height: 619px;
    background-position: center center;
    background-size: cover; }
  @include lg {
    height: 448px; }

  .footer-block-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    flex-direction: column;
    @include md {
      flex-direction: column;
      justify-content: center; }
    @include lg {
      flex-direction: row;
      justify-content: space-between; } }

  .footer-info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    justify-self: left;
    align-items: center;
    margin-bottom: 70px;
    @include md {
      margin-bottom: 80px; }
    @include lg {
      margin-bottom: 0; }

    &__title {
      font-weight: 400;
      font-size: 19px;
      line-height: 24px;
      text-align: center;
      color: #222224;
      max-width: 380px;
      @include md {
        font-size: 20px; } } }

  .footer-subscr {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    @include md {
      width: 50%; }
    @include lg {
      width: 50%; } }

  .footer-info__img {
    margin-bottom: 30px; }

  .footer-subscr__title {
    font-weight: 700;
    font-size: 24px;
    color: #222224;
    text-align: center;
    margin-bottom: .1rem; }
  .footer-subscr__subtitle {
    font-weight: 400;
    font-size: 14px;
    text-align: center;
    color: #222224;
    margin-bottom: 22px;
    @include md {
      font-size: 18px;
      margin-bottom: 2rem; }
    @include lg {
      font-size: 18px;
      margin-bottom: 2rem; } }


  .footer-subscr__input {
    display: flex;
    input {
      line-height: 50px;
      background-color: #E1E1E1;
      outline: none;
      border: 0;
      padding-left: 22px;
      border-top-left-radius: 25px;
      border-bottom-left-radius: 25px;
      width: 250px; }
    button {
      font-weight: 400;
      font-size: 14px;
      line-height: 50px;
      display: flex;
      align-items: center;
      text-align: center;
      color: #FFFFFF;
      background-color: #F16D7F;
      outline: none;
      border: 0;
      border-top-right-radius: 25px;
      border-bottom-right-radius: 25px;
      padding: 0 20px;
      cursor: pointer; } } }

.footer-block-social {
  background-color: #222224;

  .footer-soc-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column-reverse;
    padding-top: 40px;
    padding-bottom: 10px;
    @include md {
      padding: 30px 0;
      flex-direction: row; }
    @include lg {
      padding: 24px 0; } }


  .footer__copy {
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    color: #FBFBFB; }

  .footer__social {
    margin-bottom: 40px;
    @include md {
      margin-bottom: 0; } }

  .footer__social a:first-child img {
    margin-left: 0; }
  .footer__social a img {
    margin-left: 8px; } }

